import React, { useState } from 'react';
import { PieChart, Pie, Cell, Tooltip, Legend,Label } from 'recharts';
// import './ornms.css';


const COLORS = ['#a5d478', '#f8d347', '#ef563d', '#00aeef', '#FF5733', '#C70039'];

const dataString = '{"nolink": "0", "active": "0", "inactive": "5", "unprovisioned": "0", "serdown": "0", "serup": "4"}';
const dataObject = JSON.parse(dataString);

const pieData = Object.keys(dataObject).map((key, index) => {
  if (key === 'inactive') {
    return { name: key, value: parseInt(dataObject[key], 10) };
  } else {
    return { name: key, value: 0 };
  }
});

console.log("helo",pieData)
const GdChart = ({toggleDropdown,Dataget}) => {
  const [activeItems, setActiveItems] = useState(
    pieData.reduce((acc, item) => {
      acc[item.name] = true;
      return acc;
    }, {})
  );

  const handleLegendClick = (label) => {
    const originalKey = Object.keys(legendLabels).find((key) => legendLabels[key] === label);
    
    if (originalKey) {
      setActiveItems((prevState) => ({
        ...prevState,
        [originalKey]: !prevState[originalKey],
      }));
    }
  };

  // const handleClickData=()=>{
  //   Dataget(pieData)
  // } 
  

  const legendLabels = {
    active: "Good",
    nolink: "No Link",
    inactive: "Down",
    unprovisioned: "Unassigned",
    serdown: "Service Down",
    serup: "Service Up",
  };
  const legendOrder = ["active", "nolink", "inactive", "unprovisioned", "serdown", "serup"]; // Order to display


  return (
    <div className='clearfix scopecontt' style={{position:"relative"}}>
      <article className='clearfix scpbg'>
        <ul className='clearfix scoplistdropdw'>
          <li> <p style={{display:'inline-block'}}>Scope :</p> <span onClick={toggleDropdown}>Golbal <span class="fa fa-chevron-down highlightText v-align-tt iconsy"></span></span></li>
          {/* <li onClick={toggleDropdown} style={{paddingLeft:'10px'}}><span>Golbal </span><span className="arrow-down"></span></li> */}
          <li><p>Regions  : <strong style={{color:'#006fff'}}>2</strong></p></li>
        </ul>
      </article>
      <PieChart width={279} height={334} className='righttt'>
        <Pie
          data={pieData}
          dataKey="value"
          nameKey="name"
          outerRadius={120}
          innerRadius={60}
          startAngle={90}
          endAngle={450}
          label={({ cx, cy, midAngle, innerRadius, outerRadius, percent, name, value }) => {
            const RADIAN = Math.PI / 180;
            const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
            const x = cx + radius * Math.cos(-midAngle * RADIAN);
            const y = cy + radius * Math.sin(-midAngle * RADIAN);
            return value !== 0 ? (
                <text x={x} y={y} fill="white" textAnchor="middle" dominantBaseline="central" fontSize={14} fontWeight="bold">
                  {value}
                </text>
              ) : null;
          }}
          labelLine={false}
          stroke='none'
        >
          {pieData.map((entry, index) => {
            const isActive = activeItems[entry.name];
            return (
              <Cell
                key={`cell-${index}`}
                fill={isActive ? COLORS[index % COLORS.length] : 'white'}
              />
            );
          })}
           {/* <Label value={pieData[inactive]} position="center" fill="black" fontSize={16} fontWeight="bold" /> */}
          <Label value={`${dataObject.inactive}`} position="center" fill="black" fontSize={16} fontWeight="bold" />
        
        </Pie>
        
        <Legend
        wrapperStyle={{ padding: '10px', bottom: '0px', width: '331px', left: '0px',fontFamily:"Lato-Regular",paddingTop:'0',fontSize:'15px',letterSpacing:'0.5px'}}
        payload={
          pieData.slice(0,4)
            .map((entry, index) => ({
              value: legendLabels[entry.name] || entry.name, 
              type: 'square',
              color: COLORS[index % COLORS.length],
              originalName: entry.name, 
            }))
            .sort((a, b) => legendOrder.indexOf(a.originalName) - legendOrder.indexOf(b.originalName)) // Sort based on legendOrder
        }
        onClick={(e) => handleLegendClick(e.value)} 
        formatter={(value) => {
          const originalKey = Object.keys(legendLabels).find((key) => legendLabels[key] === value) || value;
          return (
            <span
              style={{
                textDecoration: activeItems[originalKey] ? 'none' : 'line-through',
              }}
            >
              {value}
            </span>
          );
        }}
        />
        <Tooltip contentStyle={{ backgroundColor: '#222', color: '#fff', borderRadius: '5px'}} itemStyle={{color:"white"}} className="tooltipmag"/>
      </PieChart>
    </div>
  );
};

 export default GdChart;

