import React from "react";
import logo from '../img/keywestlogo.png';
import { RadialBar, Tooltip, RadialBarChart } from "recharts";



const data = {
  "sta": {
    "total": 4,
    "ptmp": { "down": 4, "up": 0 },
    "backhaul": { "down": 0, "up": 0 },
    "ptp": { "down": 0, "up": 0 },
    "switch": { "down": 0, "up": 0 }
  },
  "ap": {
    "total": 1,
    "ptmp": { "down": 1, "up": 0 },
    "backhaul": { "down": 0, "up": 0 },
    "ptp": { "down": 0, "up": 0 },
    "switch": { "down": 0, "up": 0 }
  }
};

const extractValues = (obj) =>
  Object.entries(obj)
    .filter(([key]) => key !== "total" && key !== "backhaul")  
    .map(([key, value]) => ({
      name: key,
      down: value.down,
      up: value.up,
      actualValue: value.down + value.up  
    }));


const staArray = extractValues(data.sta);
const apArray = extractValues(data.ap);

const CustomTooltip = ({ active, payload }) => {
  if (active && payload && payload.length) {
    const downValue = payload[0]?.payload.down || 0;
    const upValue = payload[0]?.payload.up || 0;

    return (
      <div className="custooltipbg">
        <p className="dowvla">{`Down: ${downValue}, Up: ${upValue}`}</p>
      </div>
    );
  }
  return null;
};

const getRadialColor = (item) => {
  if (item.down > item.up) {
    return '#E74C3C';  // Red for higher 'down'
  } else if (item.up > item.down) {
    return '#2ECC71';  // Green for higher 'up'
  }
  return '#BDC3C7';  // Light gray for equal values
};

const BsChart = () => {
  return (
    <>
      {/* <article className="container"> */}
        <article className="row">
        <div className="bsucont col-3">
                <div className="clearfix bsusubcont"> 
                <span className="fas fa-broadcast-tower" style={{fontSize:'50px'}}></span>
                <h2 className="bsuheadingc">BSU <span>1</span></h2>
                </div>
                </div>
          {staArray.map((item, index) => (
            <div key={`sta-${index}`} style={{ width: '230px', height: '207px' }} className="col-4 bsucont">
              <h3 className="radialheading">{item.name}</h3>
              <RadialBarChart
                width={200}
                height={190}
                innerRadius="70%"
                outerRadius="130%"
                data={[item]}  
                startAngle={180}
                endAngle={0}
                cy={130}
                cx={100}
              >
                <RadialBar
                  minAngle={15}
                  label={{
                    fill: '#fff',          
                    position: 'inside',    
                    fontSize: 16,          
                    fontWeight: 'bold',     
                    fontFamily:'Lato-Regular'
                  }}
                  background
                  clockWise={true}
                  dataKey="actualValue"
                  fill={getRadialColor(item)}  
                />
                 <text
                  x={100}               
                  y={130}              
                  textAnchor="middle"   
                  dominantBaseline="middle"  
                  fill="black"           
                  fontSize={16} 
                  fontWeight="bold"  
                >
                  {item.actualValue}
                </text>
                <Tooltip content={<CustomTooltip />} />
              </RadialBarChart>
            </div>
          ))}
        </article>
      {/* </article> */}

      {/* <article className="container"> */}
        <article className="row">
                
            <div className="sucont col-3">
                <div className="clearfix susubcont">
                <img src={logo} alt="SU Icon" className="sulogoim"/>
                <h2 className="suheadingc">SU <span>4</span></h2>
                </div>
            </div>
          {apArray.map((item, index) => (
            <div key={`ap-${index}`} style={{ width: '230px', height: '207px' }} className="col-4 bsucont">
              <RadialBarChart
                width={200}
                height={190}
                innerRadius="70%"
                outerRadius="130%"
                data={[item]}  
                startAngle={180}
                endAngle={0}
                cx={100}
                cy={130}
              >
                <RadialBar
                  minAngle={15}
                  label={{
                    fill: '#fff',          
                    position: 'inside',    
                    fontSize: 16,          
                    fontWeight: 'bold'     
                  }}
                  background
                  clockWise={true}
                  dataKey="actualValue"
                  fill={getRadialColor(item)}  
                />
                <text
                  x={100}               
                  y={130}              
                  textAnchor="middle"   
                  dominantBaseline="middle"  
                  fill="black"           
                  fontSize={16} 
                  fontWeight="bold"  
                >
                  {item.actualValue}
                </text>
                <Tooltip content={<CustomTooltip />} />
              </RadialBarChart>
            </div>
          ))}
        </article>
      {/* </article> */}
    </>
  );
};

export default BsChart;
