// reducer.js
import { TOGGLE_VISIBILITY } from '../Action/action';

const initialState = {
  isVisible: true,  // Initially, the element is visible
};

const visibilityReducer = (state = initialState, action) => {
  switch (action.type) {
    case TOGGLE_VISIBILITY:
      return {
        ...state,
        isVisible: !state.isVisible,  // Toggle the visibility
      };
    default:
      return state;
  }
};

export default visibilityReducer;

  