const Tree = ({ data = [],getElementAtEvent }) => {
  const [selectedNode, setSelectedNode] = useState(null);
  console.log(data)

  return (
    <div className="d-tree">
      <ul className="d-flex d-tree-container flex-column neecl" style={{padding:'0',margin:"0"}}>
        {data.map((tree) => (
          <TreeNode
            key={tree.label}
            node={tree}
            selectedNode={selectedNode}arrowopen
            setSelectedNode={setSelectedNode}
            getElementAtEvent={getElementAtEvent}
          />
        ))}
      </ul>
    </div>
  );
};


const TreeNode = ({ node, selectedNode, setSelectedNode,getElementAtEvent }) => {
  const [childVisible, setChildVisibility] = useState(false);

  const hasChild = node.children ? true : false;

  return (
    <li
      className={"d-tree-node border-0"}
      onClick={(e) => {
        e.stopPropagation();
        setSelectedNode(node.label);
        getElementAtEvent(node.label)
      }}
      style={{
        marginLeft: selectedNode === node.label ==='Golbal' ? "0px" : "0px",
        // cursor: "pointer",
      }}
    >
      <div className="d-flex" style={{marginLeft:'0px'}} onClick={() => setChildVisibility((v) => !v)}>
        {hasChild && (
          <div
            className={`d-inline d-tree-toggler ${childVisible ? "active" : ""}`}
          >
            <FontAwesomeIcon icon="caret-right" />
          </div>
        )}

        <div className={`col d-tree-head globhee ${selectedNode === node.label ? "selected" : ""}`} style={{
        backgroundColor: selectedNode === node.label ? "#beebff" : "white",
        cursor: "pointer",
      }}>
          <i className="arrowopen"></i>
          <i className={`mr-1 ${node.icon}`}> </i>
          {node.label}
        </div>
      </div>

      {hasChild && childVisible && (
        <div className="d-tree-content">
          <ul className="d-flex d-tree-container flex-column neecl">
            {node.children.map((child) => ( 
              <TreeNode
                key={child.label}
                node={child}
                selectedNode={selectedNode}
                setSelectedNode={setSelectedNode}
                getElementAtEvent={getElementAtEvent}
              />
            ))}
          </ul>
        </div>
      )}
    </li>
  );
};

export default Tree;
