import React from "react";
import { useNavigate, useLocation } from "react-router-dom";
import '../ornms.css';

const LeftNavList = () => {
    const navigate = useNavigate();
    const location = useLocation();  // Get the current path

    const handleClick = (path) => {
        navigate(path);  // Navigate directly
    };

    const getActiveIndex = (path) => {
        // Match the current path with the item path and return its index
        const pathToIndex = {
            "/": 0,
            "/Discovery": 1,
            "/Topology": 2,
            "/Configuration": 3,
            "/Event": 4,
            "/Inventory": 5,
            "/SuService": 6,
            "/Setting": 7
        };
        return pathToIndex[path] ?? null;
    };

    const activeIndex = getActiveIndex(location.pathname); // Get active index from current location

    return (
            <article style={{display:"inline-block"}}>
                <ul className='leftnavlist'>
                    <li onClick={() => handleClick("/")}>
                        <i className={`fas fa-lg fa-tachometer-alt ${activeIndex === 0 ? 'activee' : ''}`}></i>
                    </li>
                    <li onClick={() => handleClick("/Discovery")}>
                        <i className={`fas fa-lg fa-crosshairs ${activeIndex === 1 ? 'activee' : ''}`}></i>
                    </li>
                    <li onClick={() => handleClick('/Topology')}>
                        <i className={`fas fa-lg fa-sitemap ${activeIndex === 2 ? 'activee' : ''}`}></i>
                    </li>
                    <li onClick={() => handleClick("/Configuration")}>
                        <i className={`fas fa-lg fa-cogs ${activeIndex === 3 ? 'activee' : ''}`}></i>
                    </li>
                    <li onClick={() => handleClick("/Event")}>
                        <i className={`fas fa-lg fa-star ${activeIndex === 4 ? 'activee' : ''}`}></i>
                    </li>
                    <li onClick={() => handleClick("/Inventory")}>
                        <i className={`fas fa-lg fa-file-alt ${activeIndex === 5 ? 'activee' : ''}`}></i>
                    </li>
                    <li onClick={() => handleClick("/SuService")}>
                        <i className={`fas fa-lg fa-bar-chart ${activeIndex === 6 ? 'activee' : ''}`} style={{ rotate: '268deg', fontSize: '18px' }}></i>
                    </li>
                    <li onClick={() => handleClick("/Setting")}>
                        <i className={`fas fa-lg fa-cog ${activeIndex === 7 ? 'activee' : ''}`}></i>
                    </li>
                </ul>
            </article>
    );
};

export default LeftNavList;



