import React, { useState } from "react";
// import './ornms.css';
import { BarChart, Bar, XAxis, YAxis,Line, Tooltip, ResponsiveContainer, Cell, CartesianGrid } from "recharts";

const datastring = {
  "severity": {
    "INDETERMINATE": 0,
    "CLEARED": 0,
    "MAJOR": 0,
    "MINOR": 1,
    "WARNING": 8,
    "NORMAL": 0,
    "CRITICAL": 0
  }
};


const customOrderf = ["CRITICAL", "MAJOR", "MINOR", "WARNING", "NORMAL", "CLEARED", "INDETERMINATE"];

const dataobj = datastring.severity;
const CustomName = {
  INDETERMINATE: "Indeterminate",
  CLEARED: "Cleared",
  MAJOR: "Major",
  MINOR: 'Minor',
  WARNING: 'Warning',
  NORMAL: 'Normal',
  CRITICAL: 'Critical'
};


const sortedData = customOrderf.map(key => ({
  name: CustomName[key],  
  displayValue: dataobj[key] === 0 ? 0.05 : dataobj[key]  
}));

const data = Object.keys(dataobj).map(key => ({
  name: CustomName[key] || key.charAt(0).toUpperCase() + key.slice(1),
  value: dataobj[key],
  displayValue: dataobj[key] === 0 ? 0.05 : dataobj[key]
}));


const CustomTooltip = ({ payload, label, active }) => {
  if (active && payload && payload.length) {
    return (
      <div className="custom-tooltip" style={{ backgroundColor: 'white', padding: '10px', border: '1px solid #ccc', minWidth: '50px' }}>
        <p style={{ padding: '0', margin: '0' }}>{label}</p>
        <p style={{ margin: '0', padding: '0' }}>{`Value: ${payload[0].value === 0.05  ? 0 : payload[0].value}`}</p>
      </div>
    );
  }
  return null;
};

const   SeverChart = () => {
  const [activeIndex, setActiveIndex] = useState(null);

  const handleMouseEnter = (index) => {
    setActiveIndex(index);
  };

  const handleMouseLeave = () => {
    setActiveIndex(null);
  };


 
    const getColor = (name) => {
      switch (name) {
        case 'Critical':
          return "#FF0000"; // Red
        case 'Major':
          return "#FF7F00"; // Orange
        case 'Minor':
          return "#FFD700"; // Yellow
        case 'Warning':
          return "#FFA500"; // Light Orange
        case 'Normal':
          return "#008000"; // Green
        case 'Cleared':
          return "#00FFFF"; // Cyan
        case 'Indeterminate':
          return "#808080"; // Gray
        default:
          return "#8884d8"; // Default color
      }
    };

  return (
    <section className="">
      <div className="">
        <div className="severitytitle">Severity Chart for Global for 24 hours</div>
        <ResponsiveContainer width="100%" height={185}>
          <BarChart data={sortedData} margin={{ top: 10, right: 30, left: 0, bottom: 5 }}>
            <CartesianGrid stroke="#ccc" strokeWidth={0.4}  vertical={true}/>

            <XAxis
              dataKey="name"
              tickLine={{ strokeWidth: 0 }}
              axisLine={{ stroke: 'black', strokeWidth: 0.2 }}
              tick={{ fill: '#0000008A', fontSize: '12px', fontWeight: 'bold', fontFamily: 'Lato-Regular',letterSpacing:'0.2px' }}
            />

            <YAxis
              domain={[0, 8]}
              ticks={[0,2,4,6,8]}
              tickLine={{ strokeWidth: 0.4, stroke: '#ccc' }}
              axisLine={{ stroke: '#ccc', strokeWidth: 0.4 }}
              tick={{ fill: '#0000008A', fontSize: '12px', fontFamily: 'Lato-Regular',letterSpacing:'0.2px'  }}
              // tickFormatter={(tick) => tick === 0 ? "0.0" : tick.toFixed(1)}
            />

            <Tooltip content={<CustomTooltip />} wrapperStyle={{
              padding: '10px',
            }} cursor={{ fill: 'transparent' }} />

            <Bar dataKey="displayValue" fill="#8884d8" barSize={30}
              onMouseEnter={handleMouseEnter}
              onMouseLeave={handleMouseLeave}
              radius={[0, 0, 0, 0]}
              opacity={activeIndex !== null ? 1 : 1}
              activeFill="red">
              {sortedData.map((entry, index) => (
                <Cell
                  key={`cell-${index}`}
                  fill={getColor(entry.name)}
                />
              ))}
            </Bar>
            {sortedData.map((entry, index) => (
              <Line
                key={`line-${index}`}
                type="monotone"
                data={[{ name: entry.name, value: entry.value }]}
                dataKey="value"
                stroke="green"
                strokeWidth={5}
                dot={true} // Remove dots
                isAnimationActive={true} // Remove animation for the lines
                activeDot={{ r: 0 }}
              />
            ))}
          </BarChart>
        </ResponsiveContainer>
      </div>
    </section>
  );
};

export default SeverChart;
